/* eslint-disable no-undef */
export function init(obj = {}) {
  if (!trackingFunctionExists('init')) {
    return;
  }

  try {
    trak.init(obj);
  } catch (e) {
    // TODO log to sentry?
  }
}

export function view(value, firstLoaded = false) {
  if (!trackingFunctionExists('view')) {
    return;
  }

  try {
    if (value) {
      trak.view(value, firstLoaded);
    } else {
      trak.view(window.location.pathname, firstLoaded);
    }
  } catch (e) {
    // TODO log to sentry?
  }
}

export function event({ c, a, l, v, state, mp, zampda, pinyou, emarsys }) {
  if (!trackingFunctionExists('event')) {
    return;
  }

  try {
    trak.event({
      category: trak.definitions.categories[c] ? trak.definitions.categories[c].name : c,
      action: trak.definitions.categories[a] ? trak.definitions.actions[a].name : a,
      label: l,
      v,
      state,
      mp,
      zampda,
      pinyou,
      emarsys,
    });
  } catch (e) {
    // TODO log to sentry?
  }
}

export function dimension(obj) {
  if (!trackingFunctionExists('dimension')) {
    return;
  }

  try {
    trak.dimension(obj);
  } catch (e) {
    // TODO log to sentry?
  }
}

export function reloadDataAttributeElement() {
  if (!trackingFunctionExists('reloadDataAttributeElement')) {
    return;
  }

  try {
    trak.reloadDataAttributeElement();
  } catch (e) {
    // TODO log to sentry?
  }
}

function trackingFunctionExists(func) {
  return typeof trak !== 'undefined' && typeof trak[func] === 'function';
}



